import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Slide from '@material-ui/core/Slide';

import { toast } from 'react-toastify';
import './styles.css';
import agent from '../../../agent';
import {

} from '../../../constants/actionTypes';
import CreateProduct from './CreateProduct'

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      user: {},
      product: { },
      images: [],
    };
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
    }
  }

  componentWillReceiveProps(np) {
    if (np.data && np.data !== this.state.product) {
      this.setState({ product: np.data });
    }
  }

  render() {
    const {
      name, desc, disc_price, price, stock, estimated_delievry, images,
    } = this.state.product;
    return (
      <div className="admin-product relative">
          <CreateProduct data={this.state.product} update/>
        <div className="product-header-img" style={{overflow:'hidden',position:'relative'}}>
          {
                images.length > 0
                  ? <img src={images[0]} alt="" /> : <img src="" alt="" />
            }
          
        </div>
        
        <div className="product-pricing">
        <h3>
            {name}
          </h3>
          {
                disc_price === price
                  ? <b>Rs. {price}</b>

                  : (
                    <div>
                      <b>Rs.{disc_price}</b>
                      <strike>{` Rs. ${price}`}</strike>

                    </div>
                  )

            }

        </div>


      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
