
import React, { Component } from 'react';
import 'tachyons';
import { connect } from 'react-redux';
import './styles.css';
import { push } from 'react-router-redux';

import { store } from '../../store';
import { APP_NAME } from '../../constants/otherConstants';
import instalink_logo from '../../assets/Images/instalink_logo.png';
import { GET_HEADER_INFO } from '../../constants/actionTypes';
import agent from '../../agent';
import ChangeHeader from '../../components/AdminPage/components/ChangeHeader';
import lightOrDark from './checkBgColor'
class Logo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      style: {},
      user: {},
      isNormalUser: true,
      isLogoEditable: false,
      headerInfo: {
        header_icon: instalink_logo,
        header_text: APP_NAME,
      },
    };
  }

  componentWillMount() {
    if (this.props.logostyles) {
      this.setState({ style: this.props.logostyles });
    } else {
      this.setState({ hideText: false });
    }
    if (this.props.parent !== 'login' && this.props.parent !== 'signup' && this.props.parent !== 'landing-page') {
      if (this.props.hideText) {
        this.setState({ hideText: this.props.hideText });
      }
      if (this.props.headerInfo) {
        this.setState({ headerInfo: this.props.headerInfo });
      } else if (this.props.user && this.props.user.user) {
        const { username, user_type } = this.props.user.user;
        const temp = user_type === 'normal';
        if (!temp) {
          const data = { username };
          this.props.getHeaderInfo(data);
        }
        this.setState({ user: this.props.user.user });
      }
      if (this.props.user && this.props.user.user) {
        const { username, user_type } = this.props.user.user;
        const temp = user_type === 'normal';
        this.setState({ isNormalUser: temp, isLogoEditable: (!temp && (this.props.parent !== 'public-home-page')) });

        this.setState({ user: this.props.user.user });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (this.props.parent !== 'login' && this.props.parent !== 'signup' && this.props.parent !== 'landing-page') {
      if (this.props.hideText) {
        this.setState({ hideText: this.props.hideText,style:np.logostyles });
      } else {
        this.setState({ hideText: false,style:np.logostyles });
      }
      if (this.props.parent === 'public-home-page') {
        const user = { username: np.username };
        agent.AdminPage.getHeaderInfo(user).then((res) => {
          if (res.data.feature) {
            if (res.data.feature.header_icon  && res.data.feature.header_text ) {
              if (res.data.feature !== this.state.headerInfo) {
                this.setState({ headerInfo: res.data.feature });
              }
            } else {
              this.setState({ headerInfo: { header_text: APP_NAME, header_icon: instalink_logo } });
            }
          } else {
            this.setState({ headerInfo: { header_text: APP_NAME, header_icon: instalink_logo } });
          }
        });
      } else if (np.headerInfo) {
        if (np.headerInfo.header_icon  && np.headerInfo.header_text ) {
          if (np.headerInfo !== this.state.headerInfo) {
            this.setState({ headerInfo: np.headerInfo });
          }
        } else {
          this.setState({ headerInfo: { header_text: APP_NAME, header_icon: instalink_logo } });
        }
        
      } else {
        this.setState({ headerInfo: { header_text: APP_NAME, header_icon: instalink_logo } });
      }

      if (np.user && np.user.user !== this.state.user) {
        const { username, user_type } = np.user.user;
        const temp = user_type === 'normal';
        this.setState({ isNormalUser: temp, isLogoEditable: (this.props.parent !== 'public-home-page') });

        this.setState({ user: np.user.user });
      }
    }
  }


  render() {
    const { header_text, header_icon } = this.state.headerInfo;
    const {
      style, isLogoEditable, isNormalUser, hideText,
    } = this.state;
    return (
      <div className="logo-img-div  " style={style}>
        <img src={header_icon} alt="" />
        {
          hideText ? null : <p>{`${header_text}`}</p>
        }

        <div className="logo-change-logo-div">

          {
            isLogoEditable ? <ChangeHeader /> : <button className="w-100 h-100" onClick={() => store.dispatch(push('/'))} />
          }

        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  headerInfo: state.AdminPageReducers.headerInfo,
  user: state.AuthReducer.user,
});

const mapDispatchToProps = (dispatch) => ({
  getHeaderInfo: (user) => dispatch({ type: GET_HEADER_INFO, payload: agent.AdminPage.getHeaderInfo(user) }),
});
export default connect(mapStateToProps, mapDispatchToProps)(Logo);
