import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';


const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: 'transparent',
    '& > div': {
      maxWidth: '100%',
      width: '100%',
      backgroundColor:'#0095f6'
    },
  },
  root:{
      background:'white'
  }
})((props) => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: 'none',
    color: '#000',
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(15),
    
    borderRight:'1px solid rgb(0,0,0,0.2)',
    borderLeft:'1px solid rgb(0,0,0,0.2)',

    width:'50%',
    // marginRight: theme.spacing(1),
    '&:focus': {
      opacity: 1,
      outline:'none',
    fontWeight:"bold"
    },
    
  },
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width:'100%',
  },
  padding: {
    // padding: theme.spacing(3),
  },
  
  demo2: {
    // backgroundColor: '#2e1534',
  },
}));

function TabPanel(props) {
  const {
    children, value, index, ...other
  } = props;
  if(value == index)
  return (
    children
  );
  return null;
}
export default function CustomizedTabs(props) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const [tabsData, setTabsData] = React.useState([]);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  React.useEffect(() => {
    setTabsData(props.tabsData);
  }, [props.tabsData]);
  return (
    <div className={classes.root}>
      <div className={classes.demo2}>
        <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
          {
            tabsData.map((t, i) => (
              <StyledTab label={t.tabHeading} key={i} />
            ))
          }


        </StyledTabs>
        {
          tabsData.map((t, i) => (
            <TabPanel value={value} key={i+15} index={i}>
              {t.components()}
            </TabPanel>

          ))
        }
      </div>
    </div>
  );
}
