/* eslint-disable func-names */
/* eslint-disable no-shadow */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react/jsx-no-bind */
/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import Slide from '@material-ui/core/Slide';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';

import { toast } from 'react-toastify';
import './styles.css';
import agent from '../../../agent';
import Loader from '../../../common/Components/Loader';
import DisabledDiv from '../../../common/Components/DisabledDiv';
import TextInput from '../../../common/Components/TextInput';
import SelectImage from '../../../common/Components/SelectImage';
import Switch from '../../../common/Components/Switch';
import {
  GET_PRODUCTS,
} from '../../../constants/actionTypes';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      mobileView: false,
      isCreateProductDialog: false,
      addImage: false,
      user: {},
      product: { images: [] },
      images: [],
      addSizesVisible: false,
      sizes: [],
    };
    this.togelIsCreateProductDialog = this.togelIsCreateProductDialog.bind(this);
    this.deleteImage = this.deleteImage.bind(this);
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data, update: this.props.update });
      const { meta_data} = this.props.data
      if (meta_data && meta_data.sizes_available) {
        this.setState({ sizes: meta_data.sizes_available, addSizesVisible: meta_data.sizes_available.length > 0 });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (np.data) {
      const { meta_data} = np.data;
      if (meta_data && meta_data.sizes_available) {
        this.setState({ sizes: meta_data.sizes_available, addSizesVisible: meta_data.sizes_available.length > 0 });
      }
      this.setState({ product: np.data });
    }
  }


  onSelectFile(file) {
    const t = [
      {
        key: 'product_id', file: this.state.product.id,
      },
      {
        key: 'photo', file: file.blob,
      },
    ];
    this.setState({ loading: true });
    agent.ProductsPage.addProductImage(t).then((res) => {
      console.group(res);
      this.setState({ loading: false });
      this.setState({ product: res.data.product });
      if (this.props.products && this.props.products.products) {
        const temp = this.props.products.products.map((p) => {
          if (p.id === res.data.product.id) {
            return res.data.product;
          }
          return p;
        });
        this.props.setProducts({ products: temp });
      } else {
        this.props.setProducts({ products: [res.data.product] });
      }
    }).catch((err) => {
      console.log(err);
      this.setState({ loading: false });

      if (err.response) {
        toast.error(err.response.error);
      } else {
        toast.error('An error ocurred in uploading the image');
      }
    });
  }

  saveChanges(e) {
    if (e) {
      e.preventDefault();
    }
    console.log(e.target);
    if (e.target.id !== 'add-size-form') {
      this.setState({ loading: true });
      const {
        name, description, price, disc_price, estimated_delivery, id,
      } = this.state.product;
      let data = {
        name, description, price: Number(price), disc_price: Number(disc_price), estimated_delivery, meta_data: { sizes_available: this.state.sizes },
      };
      console.log(data);
      if (this.state.update) {
        data = { ...data, id };
        console.log(data, this.state.product);

        if (this.state.textChanged) {
          agent.ProductsPage.updateProduct(data).then((res) => {
            console.log(res);
            this.setState({ addImage: true, loading: false, product: res.data.product });
            if (this.props.products && this.props.products.products) {
              const temp = this.props.products.products.map((p) => {
                if (p.id === res.data.product.id) {
                  return res.data.product;
                }
                return p;
              });
              this.props.setProducts({ products: temp });
            } else {
              this.props.setProducts({ products: [res.data.product] });
            }
          }).catch((err) => {
            this.setState({ loading: false });
            console.log(err);
            if (err.response) {
              console.log(err.response);
              toast.error(err.response.data.error);
            } else {
              toast.error('An error occured in updating product');
            }
          });
        } else {
          this.setState({ addImage: true, loading: false });
        }
      } else {
        agent.ProductsPage.createProduct(data).then((res) => {
          this.setState({ addImage: true, loading: false, product: res.data.product });

          if (this.props.products && this.props.products.products) {
            this.props.setProducts({ products: [...this.props.products.products, res.data.product] });
          } else {
            this.props.setProducts({ products: [res.data.product] });
          }
          console.log(res);
        }).catch((err) => {
          console.log(err);
          this.setState({ loading: false });

          if (err.response) {
            console.log(err.response);
            toast.error(err.response.data.error);
          } else {
            toast.error('An error ocurred in creating product');
          }
        });
      }
    }
  }

  togelIsCreateProductDialog() {
    this.setState({
      isCreateProductDialog: !this.state.isCreateProductDialog,
      addImage: false,

    });
  }

  deleteImage(img) {
    const data = {
      image: img,
      product_id: this.state.product.id,
    };
    this.setState({ loading: true });
    agent.ProductsPage.deleteProductImage(data).then((res) => {
      console.group(res);
      this.setState({ loading: false });
      this.setState({ product: res.data.product });
      if (this.props.products && this.props.products.products) {
        const temp = this.props.products.products.map((p) => {
          if (p.id === res.data.product.id) {
            return res.data.product;
          }
          return p;
        });
        this.props.setProducts({ products: temp });
        // this.togelIsCreateProductDialog();
      } else {
        this.props.setProducts({ products: [res.data.product] });
        // this.togelIsCreateProductDialog();
      }
    }).catch((err) => {
      console.log(err);
      this.setState({ loading: false });

      if (err.response) {
        toast.error(err.response.error);
      } else {
        toast.error('An error ocurred in uploading the image');
      }
    });
  }

  deleteProduct() {
    const data = {
      product_id: this.state.product.id,
    };
    this.setState({ loading: true });

    agent.ProductsPage.deleteProduct(data).then((res) => {
      console.log(res);
      if (this.props.products && this.props.products.products) {
        const temp = this.props.products.products.filter((p) => p.id !== data.product_id);
        this.props.setProducts({ products: temp });
      } else {
        this.props.setProducts({ products: [res.data.product] });
      }
      this.setState({ loading: false });
    }).catch((err) => {
      console.log(err);
      if (err.response) {
        toast.error(err.response.data.error);
      } else {
        toast.error('An error ocurred in deleting the product !!!');
      }
      this.setState({ loading: false });
    });
  }

  togelAddSizes() {
    this.setState({ addSizesVisible: !this.state.addSizesVisible });
  }

  addSize(e) {
    if (e) {
      e.preventDefault();
    }
    this.setState({ sizes: [...this.state.sizes, this.state.size], size: '',textChanged:true });
  }

  deleteSize(index) {
    const temp = this.state.sizes.filter((s, i) => i != index);
    this.setState({ sizes: temp ,textChanged:true});
  }

  render() {
    const {
      product, isCreateProductDialog, isNormalUser, update, addImage, addSizesVisible, sizes, size,
    } = this.state;
    if (addImage) {}
    return (
      <div>

        <button
          className=" center  flex add-new-product "

          onClick={this.togelIsCreateProductDialog.bind(this)}
        >
              Add new Product
        </button>

        <Dialog
          open={isCreateProductDialog}
          TransitionComponent={Transition}
          keepMounted
          maxWidth
          onClose={this.togelIsCreateProductDialog}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"

        >
          {
                addImage
                  ? (
                    <div>
                      <DialogTitle id="alert-dialog-slide-title">Add Product Image</DialogTitle>

                      <DialogContent style={{
                        flexWrap: 'wrap', position: 'relative', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center',
                      }}
                      >
                        <div className="flex flex-wrap justify-center ">
                          {
                              product.images.map((img, i) => (
                                <div className="product-admin-img-div">
                                  <img src={img} alt="" />
                                  <button className="" onClick={() => this.deleteImage(img)} type="button">

                                    <DeleteForeverIcon />
                                  </button>

                                </div>
                              ))
                          }

                        </div>

                      </DialogContent>
                      <DialogActions>
                        <button type="submit" color="primary" className="color-btn ma1 relative w-50">
                              Add Image
                          <SelectImage onSelectFile={this.onSelectFile.bind(this)} />
                        </button>
                        <button onClick={this.togelIsCreateProductDialog} color="primary" className="btn ba w-50">
                          Done
                        </button>

                      </DialogActions>
                    </div>
                  )
                  : (

                    <form onSubmit={this.saveChanges.bind(this)} className="create-product-form">
                      <DialogTitle id="alert-dialog-slide-title">Create Product</DialogTitle>
                      <DialogContent style={{
                        flexWrap: 'wrap', position: 'relative', display: 'flex', flexDirection: 'column', alignItems: 'center',
                      }}
                      >
                        <TextInput label="Product Name" value={product.name} type="text" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, name: text } })} required />
                        <TextInput label="Description" value={product.description} type="text" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, description: text } })} required />
                        <TextInput label="Price" value={product.price} type="number" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, price: text } })} required />
                        <TextInput label="Discounted Price" value={product.disc_price} className="w-100" type="number" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, disc_price: text } })} required />
                        {/* <TextInput label="Stock" value={product.stock} type="number" className="w-100" onTextChange={(text) => this.setState({textChanged:true, product: { ...product, stock: text } })} required /> */}
                        <TextInput label="Expected Delivery (in days)" value={product.estimated_delivery} type="number" className="w-100" onTextChange={(text) => this.setState({textChanged:true, product: { ...product, estimated_delivery: text } })} required />
                        <div className="flex items-center ">
                          <label> Product has sizes : </label>
                          <Switch togelswitch={this.togelAddSizes.bind(this)} val={addSizesVisible} />
                        </div>
                        {
                          addSizesVisible ? (
                            <form onSubmit={this.addSize.bind(this)} id="add-size-form" className='w-100' >
                              <div className="flex w-100 items-center  ">
                                <TextInput label="Add Size" value={size} type="text" className="w-80" onTextChange={(text) => this.setState({ size: text })} />
                                <button type="submit" className="color-btn w-20 h-100">Add Size</button>
                              </div>
                            </form>
                          ) : null
                        }

                        <div className="flex flex-wrap">
                          {
                            sizes.map((s, i) => (
                              <div className="size" key={i}>
                                <p>
                                  {s}
                                </p>
                                <DeleteOutlineIcon onClick={() => this.deleteSize(i)} />
                              </div>
                            ))
                          }
                        </div>
                      </DialogContent>
                      <DialogActions>
                        {
                              update
                                ? (
                                  <div>
                                    <button type="submit" color="primary" className="color-btn ma1 " style={{ flexGrow: 1 }}>
                                    Product Images
                                    </button>
                                    <button type="button" onClick={() => this.deleteProduct()} color="primary" className="color-btn ma1 " style={{ flexGrow: 1 }}>
                                    Delete Product
                                    </button>
                                  </div>
                                )
                                : (
                                  <button type="submit" color="primary" className="color-btn ma1 " style={{ flexGrow: 1 }}>
                                    Add Images
                                  </button>
                                )

                          }

                        <button onClick={this.togelIsCreateProductDialog} color="primary" className="btn ba ">
                          Cancel
                        </button>

                      </DialogActions>
                    </form>

                  )
            }


          {
            this.state.loading ? <Loader /> : null
          }
          {
            isNormalUser
              ? (
                <DisabledDiv message="Purchase a pro pack to get this utility." />
              ) : null
          }
        </Dialog>
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  products: state.ProductsPageReducers.products,

});

const mapDispatchToProps = (dispatch) => ({
  setProducts: (products) => dispatch({ type: GET_PRODUCTS, payload: products }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
