/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import './styles.css';
import { toast } from 'react-toastify';


import { GET_USER_DETAILS,SET_USER } from '../../../constants/actionTypes';
import {WEB_TOKEN} from '../../../constants/otherConstants'
import Loader from '../../../common/Components/Loader';
import agent from '../../../agent';
import ChangeProfilePic from '../../../common/Components/ChangeProfilePic';
import TextInput from '../../../common/Components/TextInput';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      myAccountDialogOpen: false,
      error: '',
      isNormalUser: true,      
      user:{name:"", email:"", username:"", phone:"", is_validated:"",}
    };
    this.handleMyAccountDialogOpen = this.handleMyAccountDialogOpen.bind(this);
  }

  componentWillMount() {
    if (this.props.user && this.props.user.user) {
      const temp = this.props.user.user.user_type === 'normal';
      this.setState({ user: this.props.user.user, isNormalUser: temp });
    }
  }

  componentWillReceiveProps(np) {
    if (np.user && np.user.user && np.user.user !== this.state.user) {
      const temp = np.user.user.user_type === 'normal';
      // console.log("user type",temp,np)
      this.setState({ user: np.user.user, isNormalUser: temp });
    }
  }

  handleMyAccountDialogOpen() {
    this.setState({ myAccountDialogOpen: !this.state.myAccountDialogOpen });
  }


  saveDetails(e) {
    e.preventDefault();
    const { name, username, email } = this.state.user;
    const data = { username, name, email };
    if (!(username.includes(" ") || username.includes("%")  || username.includes("@") )) {
      this.setState({ loading: true, error: '' });
      agent.AdminPage.updateUserDetails(data).then((res) => {
        // this.props.getUserDetails(data);
        agent.setToken(res.data.token)
        window.localStorage.setItem(WEB_TOKEN,res.data.token)
        this.props.setUser({ user: res.data.user });
        this.setState({ loading: false, error: '' });
        this.handleMyAccountDialogOpen();
      }).catch((err) => {
        console.log(err.response.data);
        if (err.response.status === 401) {
          this.setState({ loading: false, error: 'Username Already Present !!!' });
        } else {
          toast.error('Error occureed');
          this.setState({ loading: false, error: 'Error Occurred in updating details' });
        }
      });
    }
  }

  onChangeUsername(text) {
    if ((text.includes(" ") || text.includes("%") || text.includes("@") )) {
      this.setState({ user: { ...this.state.user, username: text.toLowerCase() }, error: 'username cannot contain @, space or %' });
    }else{
      this.setState({ user: { ...this.state.user, username: text.toLowerCase() }, error: '' });
    }
  }

  onChangeName(text) {
    this.setState({ user: { ...this.state.user, name: text }, error: '' });
  }

  onChangeEmail(text) {
    this.setState({ user: { ...this.state.user, email: text }, error: '' });
  }

  render() {
    const {
      name, email, username, phone, is_validated,
    } = this.state.user;
    const { isNormalUser, error } = this.state;
    
    return (
      <div className="w-100 h-100">
        <button type="button" className="side-drawer-link relative" onClick={() => this.handleMyAccountDialogOpen()}>
          <AccountCircleIcon />
            My Account

        </button>
        <Dialog
          open={this.state.myAccountDialogOpen}
          TransitionComponent={Transition}
          keepMounted
          onClose={this.handleMyAccountDialogOpen}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
          style={{ width: '100%' }}
        //   classes={{ root: { width: '100%' } }}
          fullWidth
          contentStyle={{ width: '100%', maxWidth: 'none' }}
        >

          <p className="f3 ma0 tc ">My Account</p>

          <DialogContent>


            <div>
              <ChangeProfilePic />
              <div className='center pointer  ' style={{width:'fit-content'}} >
                <ChangeProfilePic version="text" />
              </div>
              <form onSubmit={this.saveDetails.bind(this)}>

                <div className="">

                  <TextInput style={{ flexGrow: 1 }} label="Username" value={username} onTextChange={this.onChangeUsername.bind(this)} />
                  
                </div>
                <TextInput label="Name" value={name} onTextChange={this.onChangeName.bind(this)} />
                <TextInput label="email" value={email} disabled={is_validated} onTextChange={this.onChangeEmail.bind(this)} />
                <TextInput label="Phone" value={phone} disabled />
                <p className="err-txt">{error}</p>
                <button type="submit" className="green-btn w-100">Save Details</button>
              </form>
            </div>


          </DialogContent>
          <DialogActions />
          {
            this.state.loading ? <Loader /> : null
          }

        </Dialog>


      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
});

const mapDispatchToProps = (dispatch) => ({
  getUserDetails: (user) => dispatch({ type: GET_USER_DETAILS, payload: agent.Auth.getUserDetails(user) }),
  setUser: (user) => dispatch({ type: SET_USER, payload: user }),
  
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
